import React, {Fragment} from 'react';
import styled from 'styled-components';
import {Paper, Collapse, Grid, Typography} from '@material-ui/core';
import LoopIcon from '@material-ui/icons/Loop';
import IconButton from '@material-ui/core/IconButton';
import {Alert} from '@material-ui/lab';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListSubheader from '@material-ui/core/ListSubheader';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import Divider from '@material-ui/core/Divider';
import ChatRoundedIcon from '@material-ui/icons/ChatRounded';


const RefreshButton = styled(IconButton)`
    background-color: rgb(0,0,0,0);
    color: ${({ theme }) => theme.text};
    &:hover {
        background-color: ${({ theme }) => theme.accent_secondary};
        color: ${({ theme }) => theme.white};
    }
`;
const ThemedListSubheader = styled(ListSubheader)`
    background: ${({ theme }) => theme.contrast2};
    color: ${({ theme }) => theme.text}
`;
const ThemedList = styled(List)`
    background-color: ${({ theme }) => theme.contrast};
    color: ${({ theme }) => theme.text};
    border-radius: 10px;
    height:300px;
    maxHeight: 500px;
    overflow-y: scroll;
`;
const ThemedAlert = styled(Alert)`
    background-color: ${({ theme }) => theme.body};
    color: ${({ theme }) => theme.text};
`;

class CommandHistory extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showAlert: false,
            alertMsg: "",
            alertSeverity: "",
            cmd_history: []
        };
    }

    getCommandHistory = (useAlert) => {
        fetch('/api/cmdhistory', {
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
            }
        })
        .then(res => (res.ok ? res : Promise.reject(res)))
        .then(res => res.json())
        .then(json_data => {
            this.setState({cmd_history: json_data.data.cmd_history});
            if (useAlert) {
                this.setState({showAlert: true, alertMsg: "Successfully refreshed the command history list!", alertSeverity: "success"});
            }
        }).catch(e => {
            console.log(e);
            this.setState({cmd_history: []});
            this.setState({showAlert: true, alertMsg: "Error retrieving the command history list!", alertSeverity: "error"});
        });
    };

    componentDidMount() {
        this.getCommandHistory(false);
    }

    componentDidUpdate(prev_props, prev_state) {
        if (prev_props.socketData.last_cmd_output !== this.props.socketData.last_cmd_output) {
            this.getCommandHistory(false);
        }
    }

    closeAlert = () => {
        this.setState({showAlert: false, alertMsg: "", alertSeverity: ""});
    }

    render() {
        let alert = this.state.showAlert ? <ThemedAlert onClose={this.closeAlert} severity={this.state.alertSeverity}>{this.state.alertMsg}</ThemedAlert> : "";
        return (
            <Grid item xs={12} sm={4}>
                <Collapse in={this.state.showAlert}>
                    {alert}
                </Collapse>
                <ThemedList
                    component={Paper}
                    aria-labelledby="command-history-subheader"
                    subheader= {
                        <ThemedListSubheader component="div" id="command-history-subheader">
                            <ChatRoundedIcon/>
                            <Typography variant="body2\n">
                                <b> Command History</b>
                            </Typography>
                            <ListItemSecondaryAction>
                                <RefreshButton 
                                    id="refreshCmdHistoryButton" 
                                    onClick={() => this.getCommandHistory(true)}
                                    size="small"
                                >
                                    <LoopIcon />
                                </RefreshButton>
                            </ListItemSecondaryAction>
                        </ThemedListSubheader>
                    }
                >
                    {
                        this.state.cmd_history.map((item, index) => 
                        (
                            <Fragment>
                                <ListItem key={item+index} dense={true}>
                                    <ListItemText primary={<Typography>{item}</Typography>}/>
                                </ListItem>
                                <Divider/>
                            </Fragment>
                        ))
                    }
                </ThemedList>
            </Grid>
        );
    }
}

export default CommandHistory;