import React from 'react';
import {Paper, Grid, Typography} from '@material-ui/core';
import styled from 'styled-components';
import Parser from 'html-react-parser';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListSubheader from '@material-ui/core/ListSubheader';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import ForumRoundedIcon from '@material-ui/icons/ForumRounded';


const ThemedListSubheader = styled(ListSubheader)`
    background: ${({ theme }) => theme.contrast2};
    color: ${({ theme }) => theme.text}
`;
const ThemedList = styled(List)`
    background-color: ${({ theme }) => theme.contrast};
    color: ${({ theme }) => theme.text};
    border-radius: 10px;
    height: 300px;
    maxHeight: 500px;
    overflow-y: scroll;
`;

class LatestCommand extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            last_cmd_type: "",
            last_cmd: ""
        };
    }

    componentDidMount() {
        if (Object.keys(this.props.socketData).length !== 0) {
            this.setState({
                last_cmd: this.props.socketData.last_cmd_output,
                last_cmd_type: this.props.socketData.last_cmd_type,
            })
        }
    }

    componentDidUpdate(prev_props, prev_state) {
        if (prev_props.socketData.last_cmd_output !== this.props.socketData.last_cmd_output) {
            this.setState({
                last_cmd: this.props.socketData.last_cmd_output,
                last_cmd_type: this.props.socketData.last_cmd_type,
            });
        }
    }

    render() {
        return (
            <Grid item xs={12} sm={4}>
                <ThemedList
                    component={Paper}
                    aria-labelledby="latest-command-subheader"
                    subheader= {
                        <ThemedListSubheader component="div" id="latest-command-subheader">
                            <ForumRoundedIcon/>
                            <Typography variant="body2\n">
                                <b> Latest Command Output</b>
                            </Typography>
                            <ListItemSecondaryAction>
                                {this.state.last_cmd_type}
                            </ListItemSecondaryAction>
                        </ThemedListSubheader>
                    }
                >
                    <ListItem>
                        <div style={{borderRadius: "15px"}}>
                            {Parser(this.state.last_cmd)}
                        </div>
                    </ListItem>
                </ThemedList>
            </Grid>
        )
    }
}

export default LatestCommand;