import React from 'react';
import {Grid, Typography, Paper} from '@material-ui/core';
import AccountTreeRoundedIcon from '@material-ui/icons/AccountTreeRounded';
import TreeView from '@material-ui/lab/TreeView';
import styled from 'styled-components';
import NestedList from './NestedList.js';
import List from '@material-ui/core/List';
import ListSubheader from '@material-ui/core/ListSubheader';


const ThemedListSubheader = styled(ListSubheader)`
    background: ${({ theme }) => theme.contrast2};
    color: ${({ theme }) => theme.text}
`;
const ThemedList = styled(List)`
    background-color: ${({ theme }) => theme.contrast};
    color: ${({ theme }) => theme.text};
    border-radius: 10px;
    height: 300px;
    maxHeight: 500px;
    overflow-y: scroll;
`;
const ThemedTreeView = styled(TreeView)`
    padding: 5px;
`;

class OnlineUsers extends React.Component {
    constructor(props) {
        super(props);
        this.max_expand = Array.from({length: 256}, (_, i) => i+1+"")
        this.state = {
            botName: "",
            list_hash: "",
            list_items: []
        };
    }

    refreshBotName = () => {
        fetch('/api/general/name', {
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
              }
        })
        .then(res => (res.ok ? res : Promise.reject(res)))
        .then(res => res.json())
        .then(json_data => {
            this.setState({botName: json_data.data.name});
        }).catch(e => {
            console.log(e);
            this.setState({botName: 'N/A'});
        });
    };

    componentDidMount() {
        if (Object.keys(this.props.socketData).length > 0) {
            this.setState({
                list_items: this.props.socketData.server_hierarchy.hierarchy,
                list_hash: this.props.socketData.server_hierarchy.hash
            })
        }
        this.refreshBotName();
    }

    componentDidUpdate(prev_props, prev_state) {
        if (Object.keys(this.props.socketData).length > 0) {
            if (prev_props.socketData.server_hierarchy) {
                if (this.state.list_hash !== this.props.socketData.server_hierarchy.hash) {
                    this.setState({
                        list_items: this.props.socketData.server_hierarchy.hierarchy,
                        list_hash: this.props.socketData.server_hierarchy.hash
                    });
                }
            }
        }
    }

    render() {
        return (
            <Grid item xs={12} sm={4}>
                <ThemedList
                    component={Paper}
                    aria-labelledby="online-users-subheader"
                    subheader= {
                        <ThemedListSubheader component="div" id="online-users-subheader">
                            <AccountTreeRoundedIcon/>
                            <Typography variant="body2\n">
                                <b> Channels</b> 
                            </Typography>
                        </ThemedListSubheader>
                    }
                >
                    <ThemedTreeView
                        defaultExpanded={this.max_expand}>
                            <NestedList
                            key="online-users-hierarchy-key"
                            bot_name={this.state.botName}
                            items={this.state.list_items} 
                            item_label={<Typography variant="body2" gutterBottom={true} noWrap>All Channels</Typography>}
                            node_id={1}/>
                    </ThemedTreeView>
                </ThemedList>
            </Grid>
        )
    }
}

export default OnlineUsers;