import React, {Fragment} from 'react';
import styled from "styled-components"
import IconButton from '@material-ui/core/IconButton';
import LoopIcon from '@material-ui/icons/Loop';
import {Alert} from '@material-ui/lab';
import {Typography, Paper, Collapse, Grid} from '@material-ui/core';
import TreeView from '@material-ui/lab/TreeView';
import TreeItem from '@material-ui/lab/TreeItem';
import PluginMetadata from './PluginMetadata';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';
import ExpandMoreRoundedIcon from '@material-ui/icons/ExpandMoreRounded';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListSubheader from '@material-ui/core/ListSubheader';
import ListItemSecondaryAction from '@material-ui/core/ListItemSecondaryAction';
import Divider from '@material-ui/core/Divider';
import SettingsInputComponentRoundedIcon from '@material-ui/icons/SettingsInputComponentRounded';


const RefreshButton = styled(IconButton)`
    background-color: rgb(0,0,0,0);
    color: ${({ theme }) => theme.text};
    &:hover {
        background-color: ${({ theme }) => theme.accent_secondary};
        color: ${({ theme }) => theme.white};
    }
`;
const ThemedListSubheader = styled(ListSubheader)`
    background: ${({ theme }) => theme.contrast2};
    color: ${({ theme }) => theme.text}
`;
const ThemedList = styled(List)`
    background-color: ${({ theme }) => theme.contrast};
    color: ${({ theme }) => theme.text};
    border-radius: 10px;
    height: 100%;
    overflow-y: scroll;
`;
const ThemedAlert = styled(Alert)`
    background-color: ${({ theme }) => theme.body};
    color: ${({ theme }) => theme.text};
`;
const ThemedTreeItem = styled(TreeItem)`
    color: ${({ theme }) => theme.text};
    padding: 5px;
    width: 100%;
`;


class PluginsList extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            showAlert: false,
            alertMsg: "",
            alertSeverity: "",
            items: []
        }
    }

    refreshPlugins = (useAlert) => {
        fetch('/api/plugins', {
            headers: {
                'Accept': 'application/json',
                'Content-Type': 'application/json'
              }
        })
        .then(res => (res.ok ? res : Promise.reject(res)))
        .then(res => res.json())
        .then(json_data => {
            this.setState({items: json_data.data.plugins});
            if (useAlert) {
                this.setState({showAlert: true, alertMsg: "Successfully refreshed the plugins list!", alertSeverity: "success"});
            }
        }).catch(e => {
            console.log(e);
            this.setState({showAlert: true, alertMsg: "Error retrieving the plugins list!", alertSeverity: "error"});
        });
    };

    closeAlert = () => {
        this.setState({showAlert: false, alertMsg: "", alertSeverity: ""});
    }

    componentDidMount() {
        this.refreshPlugins(false);
    }

    render() {
        let alert = this.state.showAlert ? <ThemedAlert onClose={this.closeAlert} severity={this.state.alertSeverity}>{this.state.alertMsg}</ThemedAlert> : "";
        return (
            <Grid item xs={12}>
                <Collapse in={this.state.showAlert}>
                    {alert}
                </Collapse>
                <ThemedList
                    component={Paper}
                    aria-labelledby="active-plugins-subheader"
                    subheader= {
                        <ThemedListSubheader component="div" id="active-plugins-subheader">
                            <SettingsInputComponentRoundedIcon/>
                            <Typography variant="body2\n">
                                <b> Active Plugins</b>
                            </Typography>
                            <ListItemSecondaryAction>
                                <RefreshButton 
                                    id="refreshCmdHistoryButton" 
                                    onClick={() => this.refreshPlugins(true)}
                                    size="small"
                                >
                                    <LoopIcon />
                                </RefreshButton>
                            </ListItemSecondaryAction>
                        </ThemedListSubheader>
                    }
                >
                    <TreeView fullWidth>
                    {
                        this.state.items.map((item, index) => 
                        (
                            <Fragment>
                                <ListItem key={item+index} dense={true}>
                                        <ThemedTreeItem key={`plugin-tree-item-${item}`} label={<Typography>{item}</Typography>} expandIcon={<ChevronRightIcon/>} collapseIcon={<ExpandMoreRoundedIcon/>} nodeId={index} onNodeSelect={() => {}}>
                                            <PluginMetadata key={`plugin-item-${item}`} plugin_name={item} theme={this.props.theme}/>
                                        </ThemedTreeItem>
                                </ListItem>
                                <Divider/>
                            </Fragment>
                        ))
                    }
                    </TreeView>
                </ThemedList>
            </Grid>
        );
    }
    
}

export default PluginsList;